.page-hero {
    padding-top: 153px;
    padding-bottom: 80px;
    min-height: 600px;
    background-position: right -8px top;
    background-repeat: no-repeat;
    background-color: var(--color-athens);

    &__breadcrumbs {
        margin-bottom: 40px;
    }

    &__title {
        margin: 0;
        margin-bottom: 24px;
        max-width: 600px;
        font-weight: 700;
        font-size: 72px;
        line-height: 130%;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: var(--color-ebony);
    }

    &__descr {
        margin: 0;
        max-width: 600px;
        font-size: 20px;
        line-height: 150%;
        color: var(--color-storm);
    }

    &--services {
        background-image: url("../img/services-hero-bg.png");
    }

    &--services-inner {
        background-image: url("../img/services-inner-bg.png");
    }

    &--work {
        background-image: url("../img/work-hero-bg.png");
    }
}